import { ActionTypes } from "../action-types/action-types";

const cart = [];

const handleCart = (state = cart, action) => {
  const product = action.payload;
  switch (action.type) {
    case ActionTypes.ADD_ITEM:
      const exist = state.find((x) => x.service_id === product.service_id);
      if (exist) {
        return state.map((x) =>
          x.service_id === product.service_id ? { ...x, qty: x.qty + 1 } : x
        );
      } else {
        return [
          ...state,
          {
            ...product,
            qty: 1,
          },
        ];
      }

    case ActionTypes.DELETE_ITEM:
      return state.map((x) =>
        x.service_id === product.service_id ? { ...x, qty: x.qty - 1 } : x
      );

    case ActionTypes.EMPTY_CART:
      return [];

    case ActionTypes.REMOVE_PRODUCT:
      return state.filter((x) => x.service_id !== product.service_id);

    default:
      return state;
  }
};

export default handleCart;
