import React, { useEffect, useRef } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { ActiveTable } from 'active-table-react';
import axios from 'axios';
import { useState } from 'react';
import { useAlert } from "react-alert";

const Table = ({ data, title }) => {
  const alert = useAlert();

  const [oldData, setOldData] = useState(data);
  const [componentMounted, setComponentMounted] = useState(false);
  const myRef = useRef(null);

  useEffect(() => {
    setComponentMounted(true);
  }, []);

  if (!data || data.length === 0) {
    return null;
  }

  const handleChange = () => {
    if (!componentMounted) return null;
    const currentTable = myRef.current.getData();
    console.log(currentTable);
    console.log(oldData);

    let tableBody = currentTable.map((row, i) => {
      if (i === 0) return null;
      return "(" + row.map(el => {
        if (!isNaN(el) && !isNaN(parseFloat(el))) return el;
        return "'" + el + "'";
      }).join(", ") + ")";
    }).join(", ").slice(2);
    console.log(tableBody);

    axios.post('http://shop/setStatistics.php', {
      tableName: title,
      tableHeader: currentTable[0].join(", "),
      tableBody
    }, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }).then(response => {
      console.log(response.data);
      //   ,   
      if (+response.data.status === 0) {
        alert.error("  !");
        myRef.current.updateData(oldData);
      //  
      } else {
        alert.show(" !");
        setOldData(currentTable);
      };
    });
  };

  return (
    <div className="container mt-4">
      <hr/>
      <h2>{title}</h2>
      <div className="table-responsive">

        <ActiveTable
          ref={myRef}
          data={oldData}
          // onDataUpdate={handleChange}
          displayAddNewColumn={false}
          enableSort={true}
          enableFilter={true}
          enableRowAdd={true}
          enableRowDelete={true}
        />

        <div>
          <button type="submit" className="btn btn-primary mt-2" onClick={handleChange}></button>
        </div>
      </div>
    </div>
  );
};










export default Table;
