import React, { useState } from "react";
import axios from "axios";
import { Navigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { setSessionId, setAdmin } from "../redux/action";


const Login = () => {
  const dispatch = useDispatch();
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  //   
  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      //  POST-     
      const response = await axios.post('http://shop/login.php', {
        email: email,
        password: password
      }, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      });

      //    ,      
      if (response.data.status == true) {
        localStorage.setItem('sessionId', response.data.sessionId);
        localStorage.setItem('admin', response.data.admin);
        dispatch(setSessionId(response.data.sessionId));
        dispatch(setAdmin(!!response.data.admin));
        alert('Login successful!');
      }

      window.location.href = './';
    } catch (error) {
      //   ,    
      console.error('An error occurred:', error);
      alert('An error occurred. Please try again later.');
    }
  };

  if (localStorage.getItem('sessionId')) return <Navigate to="/" />

  return (
    <div className="min-vh-100 one-page">
      <div className="container min-vh-100 pt-5">
        <div className="row justify-content-center">
          <div className="col-md-6">
            <div className="card">
              <div className="card-header"></div>
              <div className="card-body">
                <form onSubmit={handleSubmit} method="post">
                  <div className="form-group">
                    <label htmlFor="email">Email</label>
                    <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} className="form-control" id="email" name="email" required />
                  </div>
                  <div className="form-group">
                    <label htmlFor="password"></label>
                    <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} className="form-control" id="password" name="password" required />
                  </div>
                  <button type="submit" className="btn btn-primary"></button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
