import React, { useEffect, useState } from 'react';
import axios from 'axios';
import Loader from '../components/Loader';
import Table from './Table';
import { useSelector } from "react-redux";
import { positions, Provider } from "react-alert";
import { Navigate } from 'react-router-dom';
import AlertTemplate from 'react-alert-template-basic'


const Statistics = () => {
  const [data, setData] = useState("");
  const admin = useSelector((state) => state.handleSession.admin);

  useEffect(() => {
    axios.get('http://shop/getStatistics.php', {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
      .then(response => {
        //        [, ()]
        let arrOfArrs = Object.entries(response.data).map(el => {
          let tableObjs = el[1];
          let tableArrs = [Object.keys(tableObjs[0]).slice(1), ...tableObjs.map(row => Object.values(row).slice(1))];
          return [el[0], tableArrs]
        });

        let data = arrOfArrs.reduce((obj, [key, value]) => {
          obj[key] = value;
          return obj;
        }, {});
        setData(data);
      })
      .catch(error => {
        console.error('There was an error fetching the data!', error);
      });
  }, []);

  if (!data) return <Loader />;
  if (!admin) return <Navigate to="/" />;
  return (
    <>
      <Provider template={AlertTemplate} timeout={3000} position={positions.BOTTOM_CENTER}>
        <Table data={data.Customers} title="Customers" />
        <Table data={data.Orders} title="Orders" />
        <Table data={data.Services} title="Services" />
        <Table data={data.OrderDetails} title="OrderDetails" />
        <Table data={data.Employees} title="Employees" />
        <Table data={data.Assignments} title="Assignments" />
        <Table data={data.Payments} title="Payments" />
        <Table data={data.Inventory} title="Inventory" />
        <Table data={data.Suppliers} title="Suppliers" />
        <Table data={data.Feedback} title="Feedback" />
      </Provider>
    </>
  );
};

export default Statistics;