import React, { useEffect, useState } from "react";
import axios from "axios";
import { Link } from "react-router-dom";

import Loader from "../components/Loader";

const Products = () => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);

  let componentMount = true;

  useEffect(() => {
    const getProducts = () => {
      setLoading(true);

      axios.get('http://shop/getServices.php').then(response => response.data).then(response => {
        setData(response);
        setLoading(false);
      }).catch(error => {
        console.error('An error occurred:', error);
      });

      return () => {
        componentMount = false;
      };
    };

    if (componentMount) getProducts();
  }, []);

  const ShowProducts = () => {
    return (
      <>
        <div className="row justify-content-center">
          {data.map((product) => {
            return (
              <div
                className="col-12 col-sm-6 col-md-4 col-lg-3 mb-4 mx-0"
                key={product.service_id}
              >
                <Link
                  to={`/products/${product.service_id}`}
                  className="card-link w-100"
                >
                  <div className="card h-100 p-2">
                    <div className="position-relative w-100 pt-100">
                      <div className="position-absolute top-0 bottom-0 w-100 h-100">
                        <img
                          src={product.image}
                          alt={product.service_name}
                          className="card-img-top img-contain"
                        />
                      </div>
                    </div>
                    <div className="card-body d-flex flex-column justify-content-between">
                      <h5 className="card-title">
                        {product.service_name.substring(0, 34)}...
                      </h5>
                      <div>
                        <p className="card-text lead fw-bold m-0">
                          ${product.price}
                        </p>
                        <p className="card-text">{product.category}</p>
                      </div>
                    </div>
                  </div>
                </Link>
              </div>
            );
          })}
        </div>
      </>
    );
  };

  return (
    <div className="container">
      <div className="row mt-4">
        <div className="col-12">
          <h1 className="display-6 fw-bolder text-center"> </h1>
          <hr />
        </div>
      </div>
      <div className="row d-flex justify-content-center">
        {loading ? <Loader /> : <ShowProducts />}
      </div>
    </div>
  );
};

export default Products;
