import React, { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link, Navigate } from "react-router-dom";
import CartProduct from "../components/CartProduct";
import Message from "../components/Message";
import { emptyCart } from "../redux/action";
import axios from "axios";

const Cart = () => {
  const dispatch = useDispatch();
  const products = useSelector((store) => store.handleCart);
  const sessionId = useSelector((state) => state.handleSession.sessionId);
  const [total, setTotal] = useState(0);

  useEffect(() => {
    setTotal(toNormalPrice(
      products
        .map((product) => product.qty * product.price)
        .reduce((prev, next) => prev + next, 0)
    ))
  }, products);


  const toNormalPrice = (price) => {
    return +price.toFixed(1).slice(-1) === 0
      ? Math.round(price)
      : price.toFixed(1);
  };

  const handleEmptyCart = () => {
    dispatch(emptyCart());
  };

  const getData = () => {
    const date = new Date();
    const year = date.getFullYear();
    const month = String(date.getMonth() + 1).padStart(2, '0');
    const day = String(date.getDate()).padStart(2, '0');
    const hours = String(date.getHours()).padStart(2, '0');
    const minutes = String(date.getMinutes()).padStart(2, '0');
    const seconds = String(date.getSeconds()).padStart(2, '0');

    return `${year}-${month}-${day} ${hours}:${minutes}:${seconds}`;
  };

  const handleSubmit = (event) => {
    event.preventDefault();

    axios.post('http://shop/setOrder.php', {
      // customer_id, order_date, total_amount, status  
      order: "('" + sessionId + "', '" + getData() + "', '" + total + "', 'in process')"
    }, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    }).then(response => {
      if (+response.data.status === 1) {
        alert(' ,  ');        
        window.location.href = './';
      } else {
        alert(' ');
      }
    });
  };


  if (!sessionId) return <Navigate to="/" />;

  const renderProducts = (
    <>
      <div id="cart">
        <div className="container my-4">
          <div className="row d-flex justify-content-between">
            <form onSubmit={handleSubmit} method="post">
              <div className="col">
                <h1 className="lead fw-bold m-0">
                  Total: ${total}
                </h1>
              </div>
              <div className="col-auto">
                <button
                  className="btn btn-outline-dark fw-bolder me-4"
                  onClick={() => handleEmptyCart()}
                >
                   
                </button>
                <button
                  type="submit"
                  className="btn btn-outline-dark fw-bolder me-4"
                >
                </button>
              </div>
            </form>
          </div>
        </div>
        {products.map((product) => (
          <CartProduct product={product} key={product.service_id} functionNormalPrice={toNormalPrice} />
        ))}
      </div>
    </>
  );

  if (products.length === 0)
    return (
      <div className="min-vh-100 one-page">
        <Message imgPath="emptyCart.png" message="You didn't choose anything" />
      </div>
    );
  else return renderProducts;
};

export default Cart;
