import React, { useState } from 'react';
import axios from 'axios';
import { useDispatch, useSelector } from "react-redux";
import { setAdmin, setSessionId } from '../redux/action';
import { Navigate } from 'react-router-dom';

const Registration = () => {
  const dispatch = useDispatch();
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  //   
  const handleSubmit = async (event) => {
    event.preventDefault();

    try {
      //  POST-     
      const response = await axios.post('http://shop/register.php', {
        name: name,
        email: email,
        password: password
      }, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      });

      //    ,      
      if (response.data.status == true) {
        let admin = response.data.admin ? "1" : "";
        localStorage.setItem('sessionId', response.data.sessionId);
        localStorage.setItem('admin', admin);
        dispatch(setSessionId(response.data.sessionId));
        dispatch(setAdmin(admin));
      }

      alert('Registration successful!');
      window.location.href = './';
    } catch (error) {
      //   ,    
      console.error('An error occurred:', error);
      alert('An error occurred. Please try again later.');
    }
  };

  if (localStorage.getItem('sessionId')) return <Navigate to="/" />

  return (
    <div className="min-vh-100 one-page">
      <div className="container min-vh-100 pt-5">
        <div className="row justify-content-center">
          <div className="col-md-6">
            <div className="card">
              <div className="card-header"></div>
              <div className="card-body">
                <form onSubmit={handleSubmit} method="post">
                  <div className="form-group">
                    <label htmlFor="name"></label>
                    <input type="text" value={name} onChange={(e) => setName(e.target.value)} className="form-control" id="name" name="name" required />
                  </div>
                  <div className="form-group">
                    <label htmlFor="email">Email</label>
                    <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} className="form-control" id="email" name="email" required />
                  </div>
                  <div className="form-group">
                    <label htmlFor="password"></label>
                    <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} className="form-control" id="password" name="password" required />
                  </div>
                  <button type="submit" className="btn btn-primary mt-5"></button>
                </form>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Registration;
