import { ActionTypes } from "../action-types/action-types";

const session = { "sessionId": null, "admin": null };

const handleSession = (state = session, action) => {
  const product = action.payload;
  switch (action.type) {
    case ActionTypes.SET_SESSIONID:
      return { ...state, "sessionId": product };

    case ActionTypes.SET_ADMIN:
      return { ...state, "admin": product };

    default:
      return state;
  }
};

export default handleSession;
